<template>
	<div>
        <el-container class="main">
            <el-aside :width="tabWidth+'px'">
                <div>
                    <div class="isClossTab" @click="isClossTabFun">
						<!-- isCollapse?'el-icon-d-arrow-right':'el-icon-d-arrow-left' -->
                        <i class="el-icon-menu"></i>
                    </div>
                    <el-menu :class="'menu'"
                            default-active="1-4-1"
                            class="el-menu-vertical-demo"
                            @open="handleOpen"
                            @close="handleClose"
							:hide-timeout="20"
                            :collapse="isCollapse"
                            text-color="#000"
                            active-text-color="#ffd04b">
						<el-menu-item index="0">
							<i class="el-icon-loading"></i>
							<span slot="title">
								<router-link class="link" :to="{ path: '/index' }">首页</router-link>
							</span>
						</el-menu-item>
                        <el-submenu index="1">
                            <template slot="title">
                                <i class="el-icon-location"></i>
                                <span slot="title">导航一</span>
                            </template>
                            <el-menu-item-group>
                                <span slot="title">分组一</span>
                                <el-menu-item index="1-1">选项1</el-menu-item>
                                <el-menu-item index="1-2">选项2</el-menu-item>
                            </el-menu-item-group>
                            <el-menu-item-group title="分组2">
                                <el-menu-item index="1-3">选项3</el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                        <el-menu-item index="2">
                            <i class="el-icon-menu"></i>
                            <span slot="title">导航二</span>
                        </el-menu-item>
                        <el-menu-item index="3">
                            <i class="el-icon-setting"></i>
                            <span slot="title">导航三</span>
                        </el-menu-item>
                    </el-menu>
                </div>
            </el-aside>
            <el-container>
                <el-header class="main-header">
					<h1 class="title">B I L I 视 频 管 理 系 统</h1>
                    <el-dropdown>
                        <span class="el-dropdown-link">
                            <img src="" alt="">
							<p class="yhmz">欢迎您：{{ adminname }}</p>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item ><a @click="logout">退出登录</a></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-header>
                <el-main>
                    <div class="">
						<el-table :data="tableData" style="width: 100%" :default-sort = "{prop: 'date', order: 'descending'}">
							<el-table-column
							  prop="date"
							  label="日期"
							  sortable
							  width="180">
							</el-table-column>
							<el-table-column
							  prop="name"
							  label="姓名"
							  sortable
							  width="180">
							</el-table-column>
							<el-table-column
							  prop="address"
							  label="地址"
							  :formatter="formatter">
							</el-table-column>
						</el-table>
					</div>
						
                    <div>
                        <router-view></router-view>
                    </div>
                </el-main>
                <el-footer class="main-footer" height="50px">
                    <p>Copyright © 2013-2019 bilibili  runoob.com All Rights Reserved.</p>
                </el-footer>
            </el-container>
        </el-container>
    </div>
</template>

<script>
	export default {
		data() {
             return {
				adminname : '',
                isCollapse: false,
                tabWidth: 200,
                test1: 1,
                intelval: null,
				tableData: [{
					date: '2016-05-02',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1518 弄'
				}, {
					date: '2016-05-04',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1517 弄'
				}, {
					date: '2016-05-01',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1519 弄'
				}, {
					date: '2016-05-03',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1516 弄'
				}]
            };
        }, 
		methods:{
			formatter(row, column){
				return row.address;
			},
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            isClossTabFun(){
                clearInterval(this.intelval);
                if(!this.isCollapse){
                    this.intelval = setInterval(()=>{
                        if(this.tabWidth<= 64)
                            clearInterval(this.intelval);
                        this.tabWidth -= 1;
                    });
                }else{
                    this.tabWidth = 200;
                }
                this.isCollapse = !this.isCollapse;
            },
			logout(){
				var self = this;
				self.$confirm('您确定要退出吗?', '退出管理平台', {
					confirmButtonText: '确定',
					cancelButtonText: '取消'
				}).then(() => {
					this.$axios.get('http://111.230.247.223/SameBWeb/xyuser/desSession.do',{params:{origin: window.location.origin}})
					.then( rs => {
						console.log(rs.data.xyuser)
						self.$message({
							message: '退出登陆成功',
							type: 'success',
							onClose: function(){
								self.$router.push({ path: '/login' });
							}
						})
					}).catch(err=>{
						
					})
				}).catch(() => {

				})
			}
        },
		mounted() {
			var self = this;
			this.$axios.get('http://111.230.247.223/SameBWeb/xyadministrator/findSession.do',{params:{origin	:  window.location.origin}})
			.then( rs => {
				self.adminname = rs.data.xyuser.adminname
			}).catch(err=>{
				
			})
		}
	}
</script>

<style>
	*{
        padding: 0;
        margin: 0;
    }
	.el-footer {
		background-color: #B3C0D1;
		color: #333;
		text-align: center;
		line-height: 60px;
	}
	.link{
		text-decoration: none;
	}
	.title{
		margin-top: 1%;
		font-size: 20px;
		text-align:center;
	}
	.yhmz{
		margin-top: -30px;
		font-size: 18px;
	}
</style>

<style lang="scss">
	$header-height:60px;
    $background-color: #324157;
    $color: #FFF;

	.main{
        height: 100vh;
        min-width: 800px;
        min-height: 600px;
        overflow: hidden;
		background-color:#f0f0f0;
        aside{
            overflow: visible;
            height: 20%;
            background-color: #07c4a8;
            color: $color;

            .isClossTab{
				color: #000000;
                width: 100%;
                height: $header-height;
                cursor: pointer;
                font-size: 25px;
                text-align: center;
                line-height: $header-height;
                font-weight: bold;
                border-right: 1px solid white;
                box-sizing: border-box;
            }
            .menu {
                width: 100%;
				height: 670px;
                border-right:0;
			}
        }

        .main-header {
            background-color: #07c4a8;
            color: $color;

            .el-dropdown{
                cursor: pointer;
                float: right;
            }
            /* .el-dropdown-link{
                img{
                    $imgMargin: (($header-height - 50)/2);
                    display:inline-block;
                    width:50px;
                    height: 50px;
                    border-radius: 25px;
                    background-color: #FFF;
                    margin-top: $imgMargin;
                }
            } */
        }

        .crumbs {
            margin-bottom: 20px;
        }

        .main-footer{
            text-align: center;
            background-color: #f0f0f0;
            color: #000000;
            line-height: 50px;
        }

    }
</style>
